@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
ul,li{
    list-style: none;
}
p{
    margin: 0;
}
.headerwrap a{
    margin:0 20px;
    color: #fff;
}
.headerwrap{
    margin-top: 10px;
}
#main{
    width: 90%;
}
#content{
    width: 800px; 
}
#sider{
    float: right;
    height: 100px;
    width: 290px;
    
    margin-bottom: 20px;
}
#header1{
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
}
#header1 a{
    margin: 0 10px;
    background-color: #80bd01;
    color: #fff;
    padding: 3px 4px;
    border-radius: 3px;
}
#li{
    padding-top: 3px;
    padding-left: 12px;
    border-left: 1px;
    border-right: 0px;
    border-style: solid;
    border-color: #ddd;
    /* border: 0 solid #ddd; */
    float: left;
    color: #999;
    cursor: default;
    background-color: transparent;
    height: 32px;
    width: 30px;
}
.inner{
    width: 270px;
    height: 96px;
    padding: 10px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
}
.span_info{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}